body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-poppins: Poppins;

  /* font sizes */
  --font-size-smi: 13px;
  --font-size-xs: 12px;
  --font-size-base: 16px;
  --font-size-sm: 14px;
  --font-size-lg: 18px;
  --font-size-mini: 15px;

  /* Colors */
  --color-whitesmoke: #f8f9fa;
  --color-darkgray-100: #a3a3a3;
  --gray-400: #a1a1aa;
  --gray-500: #71717a;
  --color-dimgray-100: #6c6c6c;
  --color-dimgray-200: #6c6c64;
  --color-silver: #c4c4c4;
  --color-gray-100: #262626;
  --gray-900: #18181b;
  --color-cornflowerblue-100: #20a6fc;
  --color-cornflowerblue-200: #058ce3;
  --fuschia-80: #f178b6;
  --fuschia-100: #ef5da8;
  --white: #fff;
  --gray-300: #d4d4d8;
  --gray-200: #e4e4e7;
  --color-gainsboro-100: #dadada;
  --fuschia-60: #fcddec;
  --gray-700: #3f3f46;

  /* Gaps */
  --gap-10xl: 29px;
  --gap-xl: 20px;
  --gap-smi: 13px;
  --gap-mid: 17px;
  --gap-6xs: 7px;
  --gap-xs: 12px;
  --gap-12xs: 1px;
  --gap-lgi: 19px;
  --gap-mini: 15px;
  --gap-base: 16px;
  --gap-lg: 18px;
  --gap-4xs: 9px;
  --gap-3xs: 10px;
  --gap-11xs: 2px;
  --gap-4xl: 23px;
  --gap-3xl: 22px;
  --gap-16xl: 35px;
  --gap-10xs: 3px;
  --gap-2xs: 11px;
  --gap-5xs: 8px;
  --gap-31xl: 50px;

  /* Paddings */
  --padding-smi: 13px;
  --padding-mid: 17px;
  --padding-xl: 20px;
  --padding-lg: 18px;
  --padding-10xs: 3px;
  --padding-sm: 14px;
  --padding-base: 16px;
  --padding-40xl: 59px;
  --padding-6xs: 7px;
  --padding-12xs: 1px;
  --padding-13xl: 32px;
  --padding-5xs: 8px;
  --padding-4xs: 9px;
  --padding-xs: 12px;
  --padding-2xs: 11px;
  --padding-3xs: 10px;
  --padding-11xs: 2px;
  --padding-5xl: 24px;
  --padding-9xs: 4px;
  --padding-2xl: 21px;
  --padding-4xl: 23px;
  --padding-lgi: 19px;
  --padding-23xl: 42px;
  --padding-12xl: 31px;
  --padding-8xs: 5px;
  --padding-mini: 15px;
  --padding-7xs: 6px;

  /* Border radiuses */
  --br-5xs: 8px;
  --br-9xs: 4px;
  --br-3xs: 10px;
  --br-xl: 20px;
  --br-11xl: 30px;

  /* Effects */
  --shadow-base: 0px 1px 2px rgba(0, 0, 0, 0.06), 0px 1px 3px rgba(0, 0, 0, 0.1);
  --shadow-inner: 0px 2px 4px rgba(0, 0, 0, 0.06) inset;
}
